import './App.css';
import React, { useState } from 'react';

function App() {

  const [currentQuestion, setCurrentQuestion] = useState(0)
  const [startGame, setStartGame] = useState(false)
  const [questions, setQuestions] = useState([])
  const [score, setScore] = useState(0)
  const [errorMessage, setErrorMessage] = useState('')
  const [showAnswer, setShowAnswer] = useState(false)

  const answerQuestion = answer => {
    if (showAnswer) return
    if (answer.correct) {
      setErrorMessage('')
      setScore(score + 1)
    }

    setShowAnswer(true)
  }

  const setUpQuestion = q => {
    setCurrentQuestion(q)
    questions[q].choices.sort(() => 0.5 - Math.random())
  }

  const doStartGame = (path, maxQuestions=40) => {
    const genrand = require('random-seed').create(new Date())
    setScore(0)
    setShowAnswer(false)
    setErrorMessage('')
    setQuestions((require(`./${path}`)).sort(() => 0.5 - genrand.floatBetween(0.0,1.0)).slice(0, maxQuestions))
    setStartGame(true)
    setUpQuestion(0)
  }

  const doNextQuestion = () => {
    const nextQuestion = currentQuestion + 1
    if (nextQuestion < questions.length) {
      setUpQuestion(nextQuestion)
    } else {
      setErrorMessage(`Z ${questions.length} otzok bolo zodpovedanch sprvne ${score} (${(score/questions.length) * 100.0}%) otzok. ${(score / questions.length < 0.75) ? "To si dojebal!" : "Gratulujem!"}`)
      setStartGame(false)
    }

    setShowAnswer(false)
  }

  return (
    <div className="App">
        <main class="my-0 mx-auto max-w-screen-lg text-center">
        <h2 class="p-6 text-4xl">{startGame ? (`Skre: ${score}`) : ("Quiz")}</h2>
        {errorMessage.length ? (
        <div class="bg-blue-100 rounded-lg py-5 px-6 mb-4 text-base text-blue-700 mb-3" role="alert">
          Hlka: {errorMessage}
        </div>
        ) : null}

        {startGame ? (
          <>
            <div class="p-6 shadow-lg rounded-lg bg-gray-100 text-gray-700">
              <p>Otzka {currentQuestion + 1} z {questions.length}</p>
              <p class="text-base font-bold">{questions[currentQuestion].question}</p>
              <div class="flex justify-center mt-6">
                {questions[currentQuestion].image ? (
                  <img src={questions[currentQuestion].image} alt="Obrzok" class="my-6" />
                ) : null}
              </div>
            </div>
            <br/>
            <div class="flex justify-center space-y-2 flex-col">
            {
              questions[currentQuestion].choices.map((choice, index) => (
                choice.text === '-' ? null : (
                <div>
                  <button
                    key={index}
                    onClick={() => answerQuestion(choice)}
                    class={"text-sm p-1 hover:brightness-110 w-full rounded-lg text-white "+(showAnswer && choice.correct ? 'bg-green-700' : showAnswer ? 'bg-red-700' : 'bg-blue-700')}>
                    {choice.text}
                  </button>
                </div>
                )))
            }
            </div>
            <div>
            { showAnswer ? (<button class="bg-green-700 hover:brightness-110 px-5 my-8 py-5 text-white rounded-lg" onClick={() => doNextQuestion()}>Pokrauj!</button>) : null }
            </div>
          </>
          ) : (
            <div class="flex justify-center">
              <button class="bg-sky-600 hover:brightness-110 px-5 py-3 m-8 text-white rounded-lg" onClick={() => doStartGame('zsr.js')}>Zani SR! (40 otzok)</button>
              <button class="bg-sky-600 hover:brightness-110 px-5 py-3 m-8 text-white rounded-lg" onClick={() => doStartGame('zsr.js', 9999)}>Zani SR!</button>
              <button class="bg-sky-600 hover:brightness-110 px-5 py-3 m-8 text-white rounded-lg" onClick={() => doStartGame('ze.js')}>Zani ZE!</button>
            </div>
          )}
      </main>
    </div>
  );
}

export default App;
